import {
  Grid,
  Typography,
  TableCell,
  TableHead,
  TableRow,
  TableBody,
  TableContainer,
  Table,
  makeStyles,
  Paper,
  TextField,
  InputAdornment,
  Button,
  Fab,
} from "@material-ui/core";
import AddIcon from "@material-ui/icons/Add";
//import Autocomplete from "@material-ui/lab/Autocomplete";
import formatMoney from "../lib/formatMoney";
import { gql, useQuery } from "@apollo/client";
import Products from "./products";

const query = gql`
  query($limit: Int) {
    allProducts(first: $limit) {
      id
      image {
        publicUrl
      }
      images {
        file {
          publicUrl
        }
      }
      name
      price
      sale
    }
  }
`;
const useStyles = makeStyles((theme) => ({
  root: {
    padding: theme.spacing(2),
    marginBottom: theme.spacing(2),
  },
}));
export default function ProductItem() {
  const classes = useStyles();
  let { data, loading, error, fetchMore, refetch } = useQuery(query, {
    variables: { limit: 6 },
  });
  let allProducts = data?.allProducts;
  return (
    <Paper className={classes.root}>
      <Grid container spacing={1}>
        <Grid item xs={12}>
          <Typography variant="body2" color="textSecondary">
            Sản phẩm:
          </Typography>
        </Grid>
        <Grid item xs={12}>
          <TextField
            fullWidth
            variant="outlined"
            size="small"
            label="Tìm kiếm"
          />
        </Grid>
        <Grid item xs={12}>
          {/* <Autocomplete
            id="combo-box-demo"
            options={[{ title: "1" }]}
            getOptionLabel={(option) => option.title}
            style={{ width: 300 }}
            renderInput={(params) => (
              <TextField {...params} label="Combo box" variant="outlined" />
            )}
          /> */}
        </Grid>

        <Grid item xs={12}>
          <TableContainer>
            <Table aria-label="simple table">
              <TableHead>
                <TableRow>
                  <TableCell></TableCell>
                  <TableCell>Tên</TableCell>
                  <TableCell align="right">Tồn</TableCell>
                  <TableCell align="right">Bình thường</TableCell>
                  <TableCell align="right">Giảm giá</TableCell>
                  <TableCell align="right"></TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {allProducts?.length
                  ? allProducts.map((product) => (
                      <TableRow key={product.id} hover onClick={() => {}}>
                        <TableCell component="th" scope="row">
                          <img
                            style={{ maxWidth: "100%", maxHeight: 80 }}
                            src={
                              product.image
                                ? product.image.publicUrl
                                : product.images[0].file.publicUrl
                            }
                          />
                        </TableCell>
                        <TableCell>{product.name}</TableCell>
                        <TableCell>{product.quantity}</TableCell>
                        <TableCell align="right">
                          {formatMoney(product.price, 0)}
                        </TableCell>
                        <TableCell align="right" color="textSecondary">
                          <Typography variant="body2" color="textSecondary">
                            {formatMoney(product.sale, 0)}
                          </Typography>
                        </TableCell>
                        <TableCell>
                          <Fab size="small" aria-label="add">
                            <AddIcon />
                          </Fab>
                        </TableCell>
                      </TableRow>
                    ))
                  : null}
              </TableBody>
            </Table>
          </TableContainer>
        </Grid>
      </Grid>
    </Paper>
  );
}
